import { Block, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import Hr from '../models';

export default {
  match(node: Node): boolean {
    return Hr.isHr(node);
  },

  convert(node: Block): JsonML {
    const data = node.data;
    if (Object.keys(data).length === 0) {
      return ['hr'];
    }
    // @ts-ignore
    return ['hr', data];
  },
};
